import React, {useEffect} from 'react' 
import {connect, useSelector} from 'react-redux'
import userActions from '../../redux/actions/userActions'
import Section from '../Section'
import User from '../User'
import Button from '../Button'
import SectionHeader from '../SectionHeader'

const UsersSection = ({users, page, initPage, showMore}) => {

    const loading = useSelector(state => state.app.loading)
    useEffect(()=>{initPage()}, [initPage])
    return (
        <Section extraClasses="section-users section_grey-bg">
            <SectionHeader 
                extraClasses="section-users__header" 
                title="Our cheerful users" 
                addition="Attention! Sorting users by registration date"
            />
            <div className = "section-users__users-wrapper">
                {users.map(user=>{
                    return (
                        <User 
                            key={user.id} 
                            img={user.photo} 
                            name={user.name} 
                            position={user.position}
                            email={user.email} 
                            phone={user.phone}
                        />
                    )
                })}
            </div>
            {loading ? <div className="lds-ripple mx-auto"><div></div><div></div></div> : ''}
            {(page.page !== page.total_pages) 
                ? (<Button 
                        handleClick = {() => showMore(page.links.next_url)} 
                        disabled={loading}  
                        extraClasses='mx-auto'
                    >
                        Show more
                    </Button>) 
                : ''}
        </Section>
    )
}

const mapStateToProps = state => {
    return state.usersList
}

export default connect(mapStateToProps, userActions)(UsersSection)