import React from 'react'
import { useSelector } from 'react-redux'
import {NavLink} from 'react-router-dom'
import shortid from 'shortid'
import logo from './img/logo.svg'
import scrollToElement from '../../helpers/scrollToElement'

const SideDrawer = ({open, drawerClickHandler}) => {
    const ref = useSelector(({app}) => app.regSectionRef)
    const linksArray1 = [
        {
            title: 'About me',
            path: '/about-me',
            ref: null,
        },
        {
            title: 'Relationships',
            path: '/relationships',
            ref: null,
        },
        {
            title: 'Users',
            path: '/users',
            ref: null,
        },
        {
            title: 'Sign Up',
            path: '/sign-up',
            ref,
        },
        {
            title: 'Terms and Conditions',
            path: '/terms-and-conditions',
            ref: null
        }
    ]
    const linksArray2 = [
        {
            title: 'How it works',
            path: '/how-it-works',
            ref: null
        },
        {
            title: 'Partnership',
            path: '/partnership',
            ref: null
        },
        {
            title: 'Help',
            path: '/help',
            ref: null
        },
        {
            title: 'Leave testimonial',
            path: '/leave-testimonial',
            ref: null
        },
        {
            title: 'Contact us',
            path: '/contact-us',
            ref: null
        }
    ]
    const linksArray3 = [
        {
            title: 'Articles',
            path: '/how-it-works',
            ref: null
        },
        {
            title: 'Our news',
            path: '/our-news',
            ref: null
        },
        {
            title: 'Testimonials',
            path: '/testimonials',
            ref: null
        },
        {
            title: 'Licenses',
            path: '/licenses',
            ref: null
        },
        {
            title: 'Privacy Policy',
            path: '/privacy-policy',
            ref: null
        }
    ]
    return (
        <div className={`side-drawer ${open ? 'open' : ''}`}>
            <nav className="side-drawer__box">
                <img src={logo} className="side-drawer__header" alt="logo"/>
                <ul className="side-drawer__links-group">
                    {linksArray1.map((linkItem) => {
                        return (
                            <li key={shortid.generate()} className="side-drawer__link">
                                <NavLink 
                                    to={linkItem.path}
                                    onClick={() => { scrollToElement(linkItem.ref) }} 
                                    activeClassName="selected" 
                                    className="parag"
                                >
                                        {linkItem.title}
                                </NavLink>
                            </li>
                        )
                    })}
                </ul>
                <ul className="side-drawer__links-group">
                    {linksArray2.map((linkItem) => {
                        return (
                            <li key={shortid.generate()} className="side-drawer__link">
                                <NavLink 
                                    to={linkItem.path}
                                    onClick={() => { scrollToElement(linkItem.ref) }} 
                                    activeClassName="selected" 
                                    className="parag"
                                >
                                    {linkItem.title}
                                </NavLink>
                            </li>
                        )
                    })}
                </ul>
                <ul className="side-drawer__links-group">
                    {linksArray3.map((linkItem) => {
                        return (
                            <li key={shortid.generate()} className="side-drawer__link">
                                <NavLink 
                                    to={linkItem.path}
                                    onClick={() => { scrollToElement(linkItem.ref) }} 
                                    activeClassName="selected" 
                                    className="parag"
                                >
                                    {linkItem.title}
                                </NavLink>
                            </li>
                        )
                    })}
                </ul>
            </nav>
            <div onClick={drawerClickHandler} className="side-drawer__substrate"></div>
        </div>
    )
}
export default SideDrawer